import BackTop from '@/components/content/backtop/BackTop.vue';

export const backTopMixin = {
    components: {
        BackTop
    },
    data() {
        return {
            // 
            backTopShow: false,
        }
    },
    methods: {
        /**
         * 
         * @param { Function } scrollTop  
         */
        scrollBackTop(scrollTop = () => {}) {
            scrollTop()
            // 
            this.backTopShow = false
        },
        // bettter-scrolldetailbettter-scroll
        scrollBackTopByBscroll() {
            this.$refs.scroll.scrollTo(0, 0)
            // 
            this.backTopShow = false
        },
        // vue-scrollerhomevue-scroller
        scrollBackTopByVscroll() {
            this.$refs.myscroller.scrollTo(0, 0, true)
            // vue-scroller
            setTimeout(() => {
                this.$refs.myscroller.scrollTo(0, 0)
            }, 10)
            // 
            this.backTopShow = false
        },

        /**
         * top > standardtop <= tandard
         * @param {*} top  
         * @param {*} standard 
         */
        judgeBackTopShow(top,standard) {
            this.backTopShow = top > standard
        }
    },
}



